import React from "react";
import Image from "next/image";
import styles from "../Home/Home.module.css";
import Link from "next/link";

async function getPosts() {
  const res = await fetch("http://127.0.0.1:8000/api/blog/posts/", {
    cache: "no-store", // or revalidate: 60 for ISR
  });
  if (!res.ok) throw new Error("Failed to fetch posts");
  return res.json();
}

const Home = async () => {
  const data = await getPosts();
  const posts = data.posts;

  return (
    <div className={styles.home}>
      <h1>Latest Contents</h1>
      <div className={styles.grid}>
        {posts.map((post) => {
          // Extract year, month, day from the publish date
          const date = new Date(post.publish); // or post.date if thatâ€™s what you have
          const year = date.getFullYear();
          const month = String(date.getMonth() + 1).padStart(2, "0");
          const day = String(date.getDate()).padStart(2, "0");

          return (
            <Link
              href={`/viewcontent/${year}/${month}/${day}/${post.slug}`}
              key={post.id}
              className={styles.card}
            >
              <div className={styles.imageWrapper}>
                <Image
                  src={
                    post.image
                      ? `https://res.cloudinary.com/dwdhdzva8/${post.image}`
                      : "/STRAWBERRY.png"
                  }
                  alt={post.title || "Post image"}
                  fill
                  className={styles.image}
                />
              </div>
              <p>{date.toDateString()}</p>
              <h2>{post.title}</h2>
              <p>{post.description}</p>
              <p>{post.category}</p>
            </Link>
          );
        })}
      </div>
      <div className={styles.seeMore}>
        <p>See more...</p>
      </div>
    </div>
  );
};

export default Home;
